interface CalendarCardInjectionType {
  isFirstLoaded?: boolean;// 首次是否已完成加载
  isLoading?: boolean;// 首次是否正在加载
  injectionMetadata?: CalendarCardMetadataType;// 最新数据，首次同步完不会主动更新。
}

export interface CalendarCardDataType extends CalendarCardInjectionType {
  cardType: string;
  height?: number;
  spacing?: {
    before?: number;
    after?: number;
  };
  metadata: CalendarCardMetadataType;
  mode?: CalendarType;
}
// 1-未响应，2-已接受，10-已拒绝, 20-待定；目前仅显示未响应跟已拒绝人员
export enum ReceiverTypeEnum {
  noresponse = 1,
  // received = 2,
  refused = 10,
  // pending = 20,
}
export enum AttachmentTypeEnum {
  text = 1,
  audio = 2,
  image = 3,
  file = 4,
  link = 5,
  redenvelop = 6, // 红包
  dingfile = 7,
}
export enum CalendarType {
  SHANHUI = 'shanhui',
}
// 接受者列表
export interface IReceiver {
  uid: number;
  name?: string;
  avatarMediaId?: string; // 头像需要经过处理
  isReceiverPlaceholder?: boolean;
  type: ReceiverTypeEnum; // 0:创建者，1:已接收；
}
export interface IOrganizerModel {
  uid: number;
  name?: string;
  avatarMediaId?: string; // 头像需要经过处理
  type: ReceiverTypeEnum; // 0:创建者，1:已接收；
}
export interface CalendarCardMetadataType {
  isPlaceholder?: boolean;
  isInitialized?: boolean; // 日程无法传递isnew参数，目前改为isInitialized
  calendarId: string;
  time?: string; // 先首次创建时国际化一下
  subject?: string;
  comment?: string;
  redirectUrl?: string; // 调整日程链接
  organizer?: IOrganizerModel[];
  topReceivers?: IReceiver[];
  totalCount?: number; // 总人数，如果totalCount>topReceivers.length 则显示 等xx人参会
  attachments?: Array<{
    type: AttachmentTypeEnum; // 1附件类型 1-text;  2-audio;  3-image;  4-file   5-link  6--红包 7--钉盘文件
    fileName?: string;
    authMediaId?: string; // 跳转链接
  }>;
  place?: string; // 优先取会议室，或者详细地点
  mode?: CalendarType;
}

export type CommonMouseEvent = (event: any) => void;

export interface ILocaleProps {
  receiverCount?: string; // 等{count}人参会
  refreshTips?: string;
  noresponse: string; // 未响应
  received: string; // 已接收
  refused: string; // 已拒绝
  pending: string; // 待定
  organizer: string; // 组织人
}
